/* CSS Variables for Consistent Theming */
:root {
  /* Color Palette */
  --primary-gradient: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  --secondary-gradient: linear-gradient(135deg, #74b9ff 0%, #0984e3 100%);
  --success-gradient: linear-gradient(135deg, #00b894 0%, #00a085 100%);
  --danger-gradient: linear-gradient(135deg, #fd79a8 0%, #e84393 100%);
  --warning-gradient: linear-gradient(135deg, #fdcb6e 0%, #e17055 100%);
  
  /* Neutral Colors */
  --text-primary: #2c3e50;
  --text-secondary: #7f8c8d;
  --text-muted: #95a5a6;
  --border-light: #e9ecef;
  --border-medium: #dee2e6;
  --background-light: #f8f9fa;
  --background-medium: #e9ecef;
  
  /* Glass Morphism */
  --glass-bg: rgba(255, 255, 255, 0.95);
  --glass-border: rgba(255, 255, 255, 0.2);
  --glass-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
  --glass-shadow-hover: 0 12px 40px rgba(0, 0, 0, 0.15);
  
  /* Typography */
  --font-heading: 'Playfair Display', serif;
  --font-body: 'Source Sans Pro', sans-serif;
  
  /* Spacing */
  --spacing-xs: 8px;
  --spacing-sm: 12px;
  --spacing-md: 16px;
  --spacing-lg: 24px;
  --spacing-xl: 32px;
  --spacing-xxl: 48px;
  
  /* Border Radius */
  --radius-sm: 8px;
  --radius-md: 12px;
  --radius-lg: 16px;
  --radius-xl: 20px;
  
  /* Transitions */
  --transition-fast: 0.2s ease;
  --transition-normal: 0.3s ease;
  --transition-slow: 0.4s ease;
}

/* Global Utility Classes */
