  align-items: flex-start;
  padding-top: 48px;
  padding-top: var(--spacing-xxl);
  z-index: 1000;
  -webkit-animation: fadeIn 0.3s ease ease-out;
          animation: fadeIn 0.3s ease ease-out;
  -webkit-animation: fadeIn var(--transition-normal) ease-out;
          animation: fadeIn var(--transition-normal) ease-out;
}

@-webkit-keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

.modal-content {
  background: rgba(255, 255, 255, 0.95);
